import React, { Component } from "react";
import PropTypes from "prop-types";

import cn from "classnames";

import { Link } from "../Atoms/Atoms";

const styles = require('./LinkComponent-styles.module.scss');

export class LinkComponent extends Component {
  static propTypes = {
    text: PropTypes.string,
    isPortfolioPage: PropTypes.bool,
    leftSide: PropTypes.bool,
  };

  state = {
    isMobile: false,
  };

  componentDidMount() {
    this.onResize();

    window.addEventListener("resize", this.onResize);
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.onResize);
  }

  onResize = () => {
    const vw = window.innerWidth;
    this.setState({ isMobile: vw <= 991 });
  };

  shouldComponentUpdate({ selectedId: nextSelectedId }, { isMobile: nextIsMobile }) {
    const { isMobile } = this.state;
    const { selectedId, id } = this.props;

    return (
      (selectedId !== nextSelectedId && (nextSelectedId === id || selectedId === id)) ||
      nextIsMobile !== isMobile
    );
  }

  render() {
    const { isMobile } = this.state;
    const {
      text,
      id,
      selectedId,
      onSectionChange,
      isPortfolioPage,
      leftSide,
    } = this.props;

    const isActive = selectedId === id;

    return (
      <Link
        onClick={() =>
          onSectionChange({
            id,
            isClickEvent: !isMobile,
            isSwipeEvent: isMobile && isPortfolioPage,
          })
        }
        isActive={isActive}
        className={cn({ [styles.isActive]: isActive && leftSide })}
      >
        {text}
      </Link>
    );
  }
}
